body {
    min-height: 100vh;
}

.wrapper {
    position: relative;
    min-height: 100vh;
    display: grid;
    grid-template: min-content 1fr min-content / 1fr;
    background: center / cover no-repeat url(${___CSS_LOADER_URL_REPLACEMENT_0___}) #FAD8D6;
}

.header {
    background-color: #2e282af4;
    color: white;
    padding: 30px 50px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.588);

    display: grid;
    grid-template: 1fr / 1fr min-content 1fr;
    align-items: center;
    gap: 20px;

    position: sticky;
    top: 0;
}

.logo {
    grid-column: 2;

    margin: 0;
    color: #EDB88B;
    font-size: 3rem;
    font-family: "Dancing Script", cursive;
}

.nav {
    justify-self: right;

    display: flex;
    flex-flow: row nowrap;

    width: clamp(200px, 100%, 300px);

    & button {
        border: none;
        background-color: inherit;
        color: inherit;
        font-size: 1.3rem;
        flex: 1;

        &:hover {
            cursor: pointer;
            color:#EDB88B;
        }

        &:not(:last-of-type) {
            border-right: 1px solid #EDB88B;
        }
    }
}

.content {
    display: grid;
    justify-items: center;
    align-items: center;

    padding: 40px;
}

.card {
    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: rgba(255, 255, 255, 0.95);
    padding: 50px;
    border-radius: 25px;
    border: 5px double#EDB88B;
    outline: 5px solid rgba(255, 255, 255, 0.95);
    margin-bottom: 40px;
    box-shadow: 11px 11px 5px rgba(0, 0, 0, 0.588);
}

.profile {
    border-radius: 50%;
    outline: 5px double#EDB88B;
    outline-offset: 2px;
}

.textbox {
    max-width: 700px;
    font-size: 1.2rem;

    h1,
    h2 {
        font-family: "Dancing Script", cursive;
        text-align: center;
    }

    h1 {
        font-size: 2rem;
        width: 100%;
        padding-bottom: 20px;
        margin: 20px 0px;
        border-bottom: 2.5px solid #EDB88B;
    }

    h2 {
        font-size: 1.5rem;
        text-decoration: underline;
    }

}

.author {
    text-align: center;
    font-size: 1.4em;
    font-family: "Dancing Script", cursive;

    &:last-child {
        margin-top: 40px;
        margin-bottom: 0;
    }
}

.category {
    /* 
        For a reason unbeknownst to me, .card.textbox.category elements
        won't respect their parent's padding without me explicitly
        subtracting it along with .card borders and outlines. 
        
        May be a browser bug, or a skill issue from my part.
    */
    width: calc(100% - 110px); 
}

.menu {
    margin: 0;
    padding: 0;
    width: 100%;

    display: flex;
    flex-direction: column;
    gap: 20px;
}

.menuItem {
    display: grid;
    grid-template: repeat(2, min-content) / 1fr min-content;
    gap: 5px 20px;

    & .name {
        font-weight: bold;
    }
}

.contact {
    max-width: 500px;
}

.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;

    color: white;
    text-shadow: 1.5px 1.5px black;
    padding: 20px 40px;

    & p {
        margin: 0;
    }

    & a {
        text-decoration: none;
        color: inherit;

        &:hover {
            color: #EDB88B;
        }
    }
}

@media (max-width: 768px) {
    .header {
        grid-template: 1fr / 1fr;
        justify-items: center;
    }

    .logo {
        grid-column: 1;
    }

    .nav {
        justify-self: center;
    }
}

@media (max-width: 425px) {
    .card {
        padding: 40px 25px;
    }
    .content {
        padding: 30px 20px;
    }
    .category {
        width: calc(100% - 60px);
    }
}

@media (max-width: 320px) {
    .card {
        padding: 30px 10px;
    }
    .content {
        padding: 20px 10px;
    }
    .category {
        width: calc(100% - 30px);
    }
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;;IAEI,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,aAAa;IACb,gDAAgD;IAChD,oFAAiE;AACrE;;AAEA;IACI,2BAA2B;IAC3B,YAAY;IACZ,kBAAkB;IAClB,4CAA4C;;IAE5C,aAAa;IACb,wCAAwC;IACxC,mBAAmB;IACnB,SAAS;;IAET,gBAAgB;IAChB,MAAM;AACV;;AAEA;IACI,cAAc;;IAEd,SAAS;IACT,cAAc;IACd,eAAe;IACf,sCAAsC;AAC1C;;AAEA;IACI,mBAAmB;;IAEnB,aAAa;IACb,qBAAqB;;IAErB,gCAAgC;;IAEhC;QACI,YAAY;QACZ,yBAAyB;QACzB,cAAc;QACd,iBAAiB;QACjB,OAAO;;QAEP;YACI,eAAe;YACf,aAAa;QACjB;;QAEA;YACI,+BAA+B;QACnC;IACJ;AACJ;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,mBAAmB;;IAEnB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;;IAEnB,2CAA2C;IAC3C,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,4CAA4C;IAC5C,mBAAmB;IACnB,8CAA8C;AAClD;;AAEA;IACI,kBAAkB;IAClB,0BAA0B;IAC1B,mBAAmB;AACvB;;AAEA;IACI,gBAAgB;IAChB,iBAAiB;;IAEjB;;QAEI,sCAAsC;QACtC,kBAAkB;IACtB;;IAEA;QACI,eAAe;QACf,WAAW;QACX,oBAAoB;QACpB,gBAAgB;QAChB,kCAAkC;IACtC;;IAEA;QACI,iBAAiB;QACjB,0BAA0B;IAC9B;;AAEJ;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,sCAAsC;;IAEtC;QACI,gBAAgB;QAChB,gBAAgB;IACpB;AACJ;;AAEA;IACI;;;;;;KAMC;IACD,yBAAyB;AAC7B;;AAEA;IACI,SAAS;IACT,UAAU;IACV,WAAW;;IAEX,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,uDAAuD;IACvD,aAAa;;IAEb;QACI,iBAAiB;IACrB;AACJ;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;;IAET,YAAY;IACZ,8BAA8B;IAC9B,kBAAkB;;IAElB;QACI,SAAS;IACb;;IAEA;QACI,qBAAqB;QACrB,cAAc;;QAEd;YACI,cAAc;QAClB;IACJ;AACJ;;AAEA;IACI;QACI,wBAAwB;QACxB,qBAAqB;IACzB;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,oBAAoB;IACxB;AACJ;;AAEA;IACI;QACI,kBAAkB;IACtB;IACA;QACI,kBAAkB;IACtB;IACA;QACI,wBAAwB;IAC5B;AACJ;;AAEA;IACI;QACI,kBAAkB;IACtB;IACA;QACI,kBAAkB;IACtB;IACA;QACI,wBAAwB;IAC5B;AACJ","sourcesContent":["html,\nbody {\n    min-height: 100vh;\n}\n\n.wrapper {\n    position: relative;\n    min-height: 100vh;\n    display: grid;\n    grid-template: min-content 1fr min-content / 1fr;\n    background: center / cover no-repeat url(img/outside.jpg) #FAD8D6;\n}\n\n.hea